$dark-grey: #2f2e33; // Сине-черный (цвет бордера)
.user {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    padding: 10px;
}

.user-info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.avatar {
    width: 110px;
    height: 110px;
    padding: 10px;
    border-radius: 50%;
    border: 1px solid $dark-grey;
    margin: 20px 0;
}